<template>
	<section id="ohmybox-component">
		<div
			class="ohmybox-gesture"
			ref="ohmyboxContainter"
			@touchstart.stop="touchStart"
			@touchmove.stop="touchMove"
			@touchend.stop="touchEnd"
			@touchcancel.stop="touchEnd"
			:style="{ webkitTransform: 'translate3d(0,' + currentPosition + 'px,0)' }"
		>
			<div class="ohmybox-item-wrap">
				<slot></slot>
			</div>
		</div>
	</section>
</template>
<script>
export default {
	name:'ohmybox',
	props: {
		// ç¿»é¡µè·ç¦»éå¼ è¶è¿å¯è§è·ç¦»åæ¢é¡µé¢ å¦ååå°åæ¥ä½ç½® åä½px
		scrollHeight: {
			default: function() {
				return 300
			}
		},
		// å¿«éæ»å¨è¿ç¨ä¸­ç¿»é¡µè·ç¦»éå¼
		quickScrollHeight: {
			default: function() {
				return 20
			}
		},
		// æ»å¨æ¶é´æç»­ å°äºè¿ä¸ªå¼ ä¼ç´æ¥ç¿»é¡µ å¦ååå°åæ¥ä½ç½® åä½ æ¯«ç§
		scrollDuration: {
			default: function() {
				return 400
			}
		},
		// itemé«åº¦ 
		itemHeight: {
			default: function() {
				return 0
			}
		}

	},
	computed: {
	},
	data: () => {
		return {
			startTime: 0, // æ»å¨å¼å§æ¶é´æ³
			page: 0, // å½åitemä¸æ 
			defaultItemHeight: window.innerHeight, // é»è®¤itemé«åº¦
			initialPosition: 0, // æ¯æ¬¡ touchStart æ»å¨åçåå§ä½ç½®
			currentPosition: 0, // touchMove è¿ç¨ä¸­çå®éä½ç½®
			startY: 0, // touchstart å¼å§æ¯ï¼ææè§¦æ§ç¹yè½´ä½ç½®
			direction: ''
		}
	},
	created() {
		this.resetOffsetHandle()
	},
	activated() {
		this.resetOffsetHandle()
	},
	methods: {
		touchStart(e) {
			this.$refs.ohmyboxContainter.classList.remove('trans')
			this.startTime = new Date().getTime()
			this.startY = e.touches[0].pageY
			this.initialPosition = this.currentPosition // æ¬æ¬¡æ»å¨åçåå§ä½ç½®
		},
		touchMove(e) {
			let deltaY = e.touches[0].pageY - this.startY
			let translate = this.initialPosition + deltaY
			this.currentPosition = translate
		},
		touchEnd(e) {
			this.$refs.ohmyboxContainter.classList.add('trans')
			let t = e.changedTouches[0].pageY
			let endTime = new Date().getTime()
			if (endTime - this.startTime < 10 && this.startY < 30) {
				// console.log('æ¨¡æç¹å»äºä»¶')
				return
			}
			if (endTime - this.startTime < this.scrollDuration) {
				if (t - this.startY > this.quickScrollHeight) {
					this.prevHandle()
				} else if (this.startY - t > this.quickScrollHeight) {
					this.nextHandle()
				} else {
					this.resetOffsetHandle()
					// console.log('ç¬¦åæ¶é´æä½ ä½æ¯ä¸ç¬¦åæç­è·ç¦»')
				}
			} else {
				let offset = t - this.startY
				if (offset > this.scrollHeight) {
					this.prevHandle()
				} else if (-offset > this.scrollHeight) {
					this.nextHandle()
				} else {
					this.resetOffsetHandle()
				}
			}
		},
		// ä¸ä¸é¡µ
		nextHandle() {
			this.direction = 'next';
			let itemLength = document.querySelector('.ohmybox-item-wrap').children.length;
			if(itemLength == this.page + 1){
				// å°è¾¾åºé¨
				this.resetOffsetHandle()
				this.$emit('change', itemLength, itemLength, this._self)
			}else{
				this.page++
				this.resetOffsetHandle()
				this.$emit('change', this.page, itemLength, this._self)
			}
		},
		// ä¸ä¸é¡µ
		prevHandle() {
			this.direction = 'prev';
			let itemLength = document.querySelector('.ohmybox-item-wrap').children.length;
			if (this.page <= 0) {
				// å°è¾¾é¡¶é¨
				this.page = 0
				this.$emit('change', this.page, itemLength, this._self)
				this.resetOffsetHandle()
			}else{
				this.page--
				this.resetOffsetHandle()
				this.$emit('change', this.page, itemLength, this._self)
			}
			
		},
		// éç½®åç§»é
		resetOffsetHandle() {
			this.currentPosition = -this.page * (this.itemHeight || this.defaultItemHeight)
		},
		//æåæ­æ¾æ¹æ³
		playerToggle() {}
	}
}
</script>
<style scoped>
#ohmybox-component {
	position: relative;
	overflow: hidden;
	width:100vw;
	height:100vh;
}
.ohmybox-gesture {
	-webkit-transform: translate3d(0, 0, 0);
	-webkit-backface-visibility: hidden;
	-webkit-perspective: 1000;
	will-change: --webkit-transform;
}
.trans {
	-webkit-transition: all 0.3s ease-out;
}
</style>
